<template>
    <div class="container">
        <div class="main__items">
            <div class="main__items-item" v-for="todo in todos" :key="todo.id">
                <h1 href="">Title: {{ todo.title}}</h1>
                <p>Completed: {{ todo.completed }}</p>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        created() {
            this.getTodos()
        },
        computed: {
            todos() {
                return this.$store.state.todos
            }
        },
        methods: {
            getTodos() {
                this.$store.dispatch('getTodos')
            }
        }

    }
</script>

